@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;0,700;1,700&display=swap);
body{color:#353535;font-family:'Montserrat', sans-serif;margin:0}

.container-home{background-color:#f5f5f5;display:flex;flex-wrap:wrap;height:calc(100vh - 80px - 45px);justify-content:center}.container-home .content-home{text-align:center;width:50%}.container-home .content-home .home-title{font-style:italic;font-size:50px;margin:0;padding:15% 0 0}.container-home .content-home .home-subtitle{font-size:23px}.container-page{background-color:#f5f5f5;height:calc(84vh - 80px - 45px);overflow-y:auto;padding:50px 0}.container-page .avatar-img{max-width:100px}.container-page .button-new-user{background:yellow;border:none;color:#353535;cursor:pointer;font-family:'Montserrat', sans-serif;left:0;outline:none;padding:15px 20px;position:fixed;transition:.2s linear}.container-page .button-new-user:active,.container-page .button-new-userfocus{background-color:#dca2a2}.container-page .button-new-user:hover{background-color:#c76b6b}.container-page .button-edit,.container-page .button-delete{border:none;color:white;cursor:pointer;font-family:'Montserrat', sans-serif;margin-right:5px;outline:none;padding:10px 15px;transition:.2s linear}.container-page .button-edit{background:teal}.container-page .button-edit:active,.container-page .button-edit:focus{background-color:#001a1a}.container-page .button-edit:hover{background-color:#004d4d}.container-page .button-delete{background:rgba(255,40,40,0.959)}.container-page .button-delete:active,.container-page .button-delete:focus{background-color:red}.container-page .button-delete:hover{background-color:red}footer{align-items:center;background-color:#C05858;display:flex;font-size:14px;height:45px;justify-content:center}

.nav{background-color:#BFDCCF;display:flex;height:80px}.nav ul{align-items:center;display:flex;justify-content:flex-start;list-style:none;margin:0}.nav ul li{display:inline-block;padding:5px 20px}.nav ul li .icon-nav{padding-right:20px;width:50px}.nav ul li .link{color:#353535;font-weight:600;text-decoration:none;transition:.1s linear}.nav ul li .link:hover{color:#C05858}

.table{background:white;border-collapse:collapse;width:100%}.table thead{border-top:1px solid #ddd;box-shadow:0 5px 5px #AAA}.table thead th{background-color:transparent;color:#333;font-weight:600;line-height:1.4;padding-bottom:18px;padding-top:18px;text-transform:capitalize}.table tbody tr{border-bottom:1px solid #AAA}.table tbody tr td{background-color:transparent;color:#333;font-weight:300;line-height:1.4;padding-bottom:18px;padding-top:18px;text-align:center}.table tbody tr:last-child{border:none}

.overlay{background-color:rgba(0,0,0,0.5);height:100vh;left:0;position:fixed;top:0;width:100vw}.modal{background:white;border-radius:4px;box-sizing:border-box;left:calc(50vw - 300px);padding:15px;position:fixed;top:10vw;width:600px;z-index:100}.modal header{align-items:center;border-bottom:1px solid #ccc;display:flex;justify-content:space-between;padding:10px}.modal header .modal-title{margin:0}.modal .close-button{background-color:transparent;border:none;cursor:pointer;outline:none}.modal .close-button img{width:18px}.modal .modal-content{padding:15px}.modal .modal-content input,.modal .modal-content select{border:1px solid rgba(0,0,0,0.25);box-sizing:border-box;font-family:'Montserrat', sans-serif;margin-bottom:20px;padding:13px 13px 13px 8px;width:100%}.modal .modal-content input:focus,.modal .modal-content select:focus{outline:1px solid rgba(0,0,0,0.2)}.modal .modal-content .button-modal{background-color:#C05858;border:none;border-radius:5px;color:white;cursor:pointer;font-family:'Montserrat', sans-serif;font-size:15px;font-weight:600;outline:none;padding:13px;text-transform:uppercase;transition:.3s linar;width:100%}.modal .modal-content .button-modal:hover{background-color:#dca2a2}

.content-container{background-color:white;border:1px solid #ccc;border-radius:8px;box-sizing:border-box;margin:0 auto;max-width:900px;padding:25px}

